const paymentValidator = data => {
    let error = {}

    if (!data.amount) {
        error.amount = 'required!'
    }
    if (!data.bank_name) {
        error.bank_name = 'required!'
    }
    if (!data.branch_name) {
        error.branch_name = 'required!'
    }
    if (!data.check_no) {
        error.check_no = 'required!'
    }
    if (!data.check_date) {
        error.check_date = 'required!'
    }
    if (!data.payment_type) {
        error.payment_type = 'required!'
    } else if (data.payment_type === 'Partial' && !data.partial_amount) {
        error.partial_amount = 'required!'
    }
    if (!data.attachment) {
        error.attachment = 'required!'
    }

    return {
        isValid: Object.keys(error).length === 0 ? true : false,
        error
    }
}
export default paymentValidator